body {
  height: 500px;
  width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  height: 400px;
  width: 400px;
  background-color: azure;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 5px 5px 5px green;
}
.display {
  width: 100%;
  height: 50px;
  background-color: aqua;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  text-shadow: 3px 3px 3px white;
  font-size: xx-large;
  border-radius: 30px;
}
.getotp {
  width: 100%;
  height: 50px;
  margin: 30px 15px;
}
.go {
  width: 90%;
  height: 35px;
  background-color: black;
  color: blue;
  border-radius: 10px;
}
.otp{
  height: auto;
  width: 100%;
  margin: 30px 60px;
  box-sizing: border-box;
  
}
#note {
  color: red;
  font-style: italic;
  font-size: larger;
}
.btns {
  margin: 30px 30px;
}
#sub,#res {
  margin: 13px;
  width: 100px;
  border: 2px solid black;
  border-radius: 20px;
  background-color: cornflowerblue;
  color:black;
}